.no-scroll {
  overflow: hidden;
}
.te-product-overlay {
  background: #b4b4b4;
  background-color: rgba(180, 180, 180, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 8000;
  overflow-y: scroll;
  min-width: 320px;
}
.te-product-card-modal {
  -webkit-box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  z-index: 9000;
  margin: 0 auto;
  position: relative;
  top: 10px;
  max-width: 2500px;
  width: calc(100% - 20px);
  background: white;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-backface-visibility: hidden;
  display: block;
}
.te-product-card-modal.display-block {
  display: block;
}
.te-product-card-modal.display-none {
  display: none;
}
@media (min-width: 768px) {
  .te-product-card-modal {
    width: 90%;
    max-width: 1200px;
  }
}
.te-product-card-modal-inner .product {
  margin-top: 0;
  padding-top: 0;
}
.te-product-card-modal-inner .te-product-info-container {
  padding: 0;
}
@-webkit-keyframes scaleout {
  0% {
    -webkit-transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
@keyframes scaleout {
  0% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
.te-product-card-modal-cart {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100px;
  margin-left: auto;
  margin-right: 1%;
  padding: 10px 0;
}
.te-product-card-modal-cart .te-button-cross {
  position: relative;
  top: unset;
  right: unset;
}
.te-tipser-product-wrapper {
  min-height: 50vh;
}
.te-product-card {
  padding: 10px;
  cursor: pointer;
  overflow: hidden;
  box-sizing: border-box;
  font-size: inherit;
}
a.te-product-card:hover {
  color: inherit;
}
.te-product-card .te-button {
  line-height: 2.08em;
  min-width: auto;
}
.te-product-card .te-button-text {
  font-size: 0.9em;
  font-weight: 400;
}
.te-product-card .te-button.disabled {
  border: 1px solid #eee;
}
.te-product-card .te-button.disabled .te-button-body {
  background-color: #f2f2f2;
}
.te-product-card .te-button.disabled .te-button-text {
  color: #ccc;
}
.te-product-card .te-product-discount-label {
  display: none;
}
.te-product-card .te-product-card-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 2.5em;
  padding: 15px 0;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;
  height: auto;
  cursor: default;
}
.te-product-card .te-product-card-img {
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 0.3s ease;
}
.te-product-card .te-product-card-img-invalid {
  background-color: #dddddd;
  width: 75%;
  max-width: 225px;
  margin: 0 auto;
  height: 100%;
}
.te-product-card .te-product-card-img:hover {
  opacity: 0.9;
}
.te-product-card .te-product-card-name {
  font-size: 1em;
  font-weight: normal;
  line-height: 1.3em;
  white-space: nowrap;
  text-overflow: ellipsis;
  letter-spacing: 0px;
  color: #5f5f5f;
  overflow: hidden;
  max-width: 100%;
  text-align: left;
  display: inline-block;
}
.te-product-card .te-product-card-name:hover {
  color: #525860;
}
.te-product-card .te-product-card-brand {
  font-size: 1em;
  cursor: default;
  font-weight: normal;
  letter-spacing: 0;
  color: #999;
  text-transform: uppercase;
  white-space: nowrap;
}
.te-product-card .te-product-card-price {
  align-items: center;
  color: #5f5f5f;
  display: flex;
  font-size: 1.08em;
  font-weight: 600;
  justify-content: center;
  margin-top: 0.5em;
}
.te-product-card .te-product-card-unit-price {
  color: #afafaf;
  line-height: 1em;
  margin-bottom: 1em;
}
.te-product-card .te-product-card-sales-price {
  color: #cd5550;
  font-weight: 600;
  font-size: 1.08em;
}
.te-product-card .te-product-card-regular-price {
  font-size: 1em;
  font-weight: 400;
  margin-left: 10px;
  text-decoration: line-through;
}
.te-product-card-compact {
  max-width: 300px;
  width: 100%;
  display: block;
  padding: 0;
  margin: 30px auto;
  font-size: inherit;
}
.te-product-card-compact .te-button {
  font-size: 0.8em;
}
.te-collection .te-product-card-small .te-img {
  height: 180px;
}
.te-collection .te-product-card-large .te-img {
  height: 240px;
}
