.te-footer-container {
  box-sizing: border-box;
  font-family: inherit;
  font-size: 14px;
  text-align: center;
  border-top: 1px solid #dcdee0;
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .te-footer-container {
    font-size: inherit;
  }
}
.te-footer-container .te-footer {
  padding: 20px 15px;
  display: flex;
  justify-content: center;
  color: #444;
}
@media (min-width: 768px) {
  .te-footer-container .te-footer {
    padding: 20px;
  }
}
.te-footer-container .te-footer .te-footer-payment-title {
  font-weight: bold;
  margin: 10px 0;
  font-size: 16px;
  letter-spacing: 0.5px;
}
.te-footer-container .te-footer .te-footer-payment-shopping {
  display: none;
  text-align: left;
  margin: 0;
}
@media (min-width: 768px) {
  .te-footer-container .te-footer .te-footer-payment-shopping {
    display: block;
  }
}
.te-footer-container .te-footer .te-footer-payment-shopping .te-payment-infos {
  padding: 0;
  display: flex;
  flex-direction: column;
  font-size: 0.8em;
  line-height: 1.6em;
}
.te-footer-container .te-footer .te-footer-payment-shopping .te-payment-infos .te-payment-info {
  display: flex;
  align-items: center;
  line-height: 2.5em;
}
.te-footer-container .te-footer .te-footer-payment-shopping .te-payment-infos .te-payment-info svg {
  width: 20px;
  height: 20px;
  margin-right: 7px;
}
.te-footer-container .te-footer .te-footer-payment-contact {
  text-align: right;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .te-footer-container .te-footer .te-footer-payment-contact {
    margin: 0;
    justify-content: flex-start;
  }
}
.te-footer-container .te-footer .te-footer-payment-contact .te-payment-link {
  font-size: 0.8em;
  line-height: 2.4em;
}
.te-footer-container .te-footer .te-footer-payment-contact .te-payment-link a {
  text-transform: none;
  text-decoration: none;
  color: #b8a056;
}
.te-footer-container .te-footer .te-footer-payment-logos {
  margin-right: auto;
  display: flex;
  flex-direction: column;
  text-align: left;
}
@media (min-width: 768px) {
  .te-footer-container .te-footer .te-footer-payment-logos {
    margin: 0 auto;
    text-align: center;
  }
}
.te-footer-container .te-footer .te-footer-payment-logos .te-payment-logos {
  display: flex;
  justify-content: center;
  align-items: center;
}
.te-footer-container .te-footer .te-footer-payment-logos .te-payment-logos :not(:first-child):not(:last-child) {
  margin: 0 10px;
}
.te-footer-container .te-footer .te-footer-payment-logos .te-payment-logos .te-payment-logo {
  border-radius: 5px;
  width: 36px;
  height: 20px;
  padding: 10px 0;
  padding: 10px 5px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 425px) {
  .te-footer-container .te-footer .te-footer-payment-logos .te-payment-logos .te-payment-logo {
    width: 40px;
    height: 30px;
  }
}
@media (min-width: 768px) {
  .te-footer-container .te-footer .te-footer-payment-logos .te-payment-logos .te-payment-logo {
    width: 50px;
  }
}
@media (min-width: 1024px) {
  .te-footer-container .te-footer .te-footer-payment-logos .te-payment-logos .te-payment-logo {
    width: 60px;
  }
}
.te-footer-container .te-footer .te-footer-payment-logos .te-payment-logos .te-payment-logo > svg {
  width: 100%;
  height: 100%;
}
.te-footer-container a {
  border-bottom: 1px solid transparent;
  transition: border-color 400ms ease;
}
.te-footer-container a:hover {
  border-bottom: 1px solid #b8a056;
}
.te-footer-terms {
  font-size: 11px;
  padding: 15px;
  background: #f8f8f8;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 425px) {
  .te-footer-terms {
    font-size: 14px;
  }
}
@media (min-width: 768px) {
  .te-footer-terms {
    padding: 20px;
  }
}
.te-footer-terms .te-terms-links {
  text-align: right;
  display: flex;
  flex-direction: column;
  line-height: 2.5em;
}
@media (min-width: 768px) {
  .te-footer-terms .te-terms-links {
    flex-direction: row;
    align-items: center;
  }
}
.te-footer-terms .te-terms-links .te-terms-link {
  display: inline-block;
  cursor: pointer;
}
@media (min-width: 768px) {
  .te-footer-terms .te-terms-links .te-terms-link {
    margin-left: 10px;
  }
}
.te-footer-terms .te-terms-links .te-terms-link a {
  color: #b8a056;
}
.te-footer-terms .te-terms-year {
  display: flex;
  align-items: center;
  white-space: nowrap;
}
@media (min-width: 768px) {
  .te-footer-terms .te-terms-year {
    margin-left: 0;
  }
}
.te-footer-logo {
  padding: 17px 15px;
  background: #f8f8f8;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}
@media (min-width: 768px) {
  .te-footer-logo {
    padding: 17px 20px;
  }
}
.te-footer-logo .te-footer-logo-img {
  width: 100px;
  transform: translateY(2px);
}
.te-footer-logo .te-footer-logo-img img {
  display: block;
  width: 100%;
}
.te-footer-logo .te-footer-shop-img img {
  height: 15px;
}
@media (min-width: 425px) {
  .te-footer-logo .te-footer-shop-img img {
    height: 22px;
  }
}
.te-footer-logo .te-footer-icons-img {
  display: flex;
  flex-direction: row;
}
.te-footer-logo .te-footer-icons-img .te-footer-icon-img {
  margin-left: 10px;
  width: 20px;
}
.te-footer-logo .te-footer-icons-img .te-footer-icon-img svg {
  height: 20px;
}
.te-footer-logo .te-footer-logo-text {
  font-size: 0.6em;
  margin: 0 2px;
}
@media (min-width: 425px) {
  .te-footer-logo .te-footer-logo-text {
    font-size: 0.7em;
    margin: 0 10px;
  }
}
