.te-header {
  position: absolute;
  top: 15px;
}
.te-header .te-header-logo {
  margin-left: 10px;
}
@media (min-width: 768px) {
  .te-header .te-header-logo {
    margin-left: 20px;
  }
}
.te-header .te-header-logo img {
  display: block;
  max-height: 30px;
  max-width: 120px;
}
@media (min-width: 768px) {
  .te-header .te-header-logo img {
    max-height: 50px;
    max-width: 140px;
  }
}
