.te-added-to-cart .te-popup-header {
  margin-right: 25px;
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1em;
}
.te-added-to-cart .te-popup-header h3 {
  font-family: inherit;
  font-size: 1em;
  margin: 0;
}
.te-added-to-cart .te-popup-img-container {
  text-align: center;
  margin-bottom: 1em;
}
.te-added-to-cart .te-popup-img-container > img {
  max-width: 100%;
  max-height: 20vh;
}
.te-added-to-cart .te-popup-product-basic-info {
  font-size: 0.8em;
  margin-bottom: 1em;
}
.te-added-to-cart .te-popup-title {
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .te-added-to-cart-header {
    margin-bottom: 1em;
  }
  .te-added-to-cart-img-container {
    display: none;
  }
}
