.tipser-ie .te-container.te-container-vertical {
  display: block !important;
}
.tipser-ie .te-container-item {
  padding: 5px;
}
.tipser-ie .te-related-page-img-wrapper {
  overflow: hidden;
}
.tipser-ie .te-related-page-img-wrapper img {
  width: auto;
  max-width: none;
  margin-left: 50%;
  transform: translateX(-50%);
  max-height: 100%;
}
.te-primary-color {
  color: #222;
}
.te-primary-fill {
  background-color: #222;
  color: #fff;
}
.te-primary-border {
  border-color: #222;
}
.te-primary-border-hover:hover {
  border-color: #222;
}
.tipser-overlay {
  height: 100vh;
}
.markdown-content {
  max-width: 940px;
  margin: 0 auto;
  overflow-x: hidden;
  padding: 15px;
}
@media (max-width: 479px) {
  .markdown-content {
    padding: 15px;
  }
}
@media (min-width: 480px) and (max-width: 959px) {
  .markdown-content {
    margin-top: 20px;
    padding: 20px;
  }
}
@media (min-width: 960px) {
  .markdown-content {
    margin-top: 20px;
    padding: 30px;
  }
}
.tipser-elements {
  font-family: 'Montserrat', 'Open Sans', sans-serif;
}
.tipser-elements .container {
  width: 1200px;
  margin: 0 auto;
}
.tipser-elements h1,
.tipser-elements h2,
.tipser-elements h3 {
  font-family: 'Lora', serif;
  margin: 10px 0;
  padding: 0;
  letter-spacing: 0.5px;
  color: #262626;
}
.tipser-elements h1 {
  font-size: 28px;
}
.tipser-elements h2 {
  font-size: 24px;
}
.tipser-elements h3 {
  font-size: 18px;
}
.tipser-elements p {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.7;
  margin: 5px 0;
  color: #262626;
}
.tipser-elements a:not(.te-button) {
  text-decoration: none;
  color: inherit;
}
.tipser-elements a:not(.te-button):focus,
.tipser-elements a:not(.te-button):visited,
.tipser-elements a:not(.te-button):active {
  color: #262626;
}
.tipser-elements a:not(.te-button):hover {
  transition: color 0.5s;
  color: #3c78c8;
}
.tipser-elements p > a {
  color: #3c78c8;
  text-decoration: underline;
}
.tipser-elements img {
  max-width: 100%;
}
.tipser-elements blockquote {
  font-weight: 600;
  margin: 2em 7vw;
}
.tipser-elements blockquote p {
  font-size: 1.5em;
}
.tipser-elements .content-section {
  padding-bottom: 30px;
  position: relative;
}
@media (max-width: 767px) {
  .tipser-elements .content-section {
    padding-bottom: 0;
    background-color: transparent;
  }
}
@media (max-width: 767px) {
  .tipser-elements .content-section {
    background-color: #fff;
  }
}
@media (min-width: 768px) {
  .tipser-elements h1 {
    font-size: 32px;
  }
  .tipser-elements h2 {
    font-size: 28px;
  }
  .tipser-elements h3 {
    font-size: 24px;
  }
  .tipser-elements p {
    font-size: 16px;
  }
}
@media (min-width: 960px) {
  .tipser-elements h1 {
    font-size: 36px;
  }
  .tipser-elements h2 {
    font-size: 32px;
  }
  .tipser-elements h3 {
    font-size: 28px;
  }
  .tipser-elements p {
    font-size: 16px;
  }
  .tipser-elements blockquote p {
    font-size: 2vw;
  }
}
@media (min-width: 1280px) {
  .tipser-elements h1 {
    font-size: 45px;
  }
  .tipser-elements h2 {
    font-size: 36px;
  }
  .tipser-elements h3 {
    font-size: 32px;
  }
  .tipser-elements p {
    font-size: 18px;
  }
  .tipser-elements blockquote p {
    font-size: 2em;
  }
}
.hide-mobile {
  display: none;
}
.show-mobile {
  display: block;
}
@media (min-width: 768px) {
  .hide-mobile {
    display: block;
  }
  .show-mobile {
    display: none;
  }
}
