.cart-icon {
  display: inline-flex;
  width: 50px;
  height: 40px;
  position: relative;
  outline: none;
  justify-content: center;
  align-items: flex-start;
}
.cart-icon .te-button-clear {
  width: 40px;
}
.cart-icon .te-button-clear svg {
  pointer-events: none;
  width: 100%;
}
.cart-icon:hover .tipser-cart-size-indicator {
  transform: translateY(-2px) scale(1.03);
}
.tipser-cart-size-indicator {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-size: 11px;
  font-weight: 600;
  background: black;
  color: white;
  box-sizing: border-box;
  position: absolute;
  bottom: 0px;
  right: 1px;
  width: 18px;
  height: 18px;
  line-height: 18px;
  text-align: center;
  border-radius: 13px;
  text-shadow: 0 0 1px #0003;
  letter-spacing: -0.5px;
  box-shadow: 0.5px 0.5px 1px #0005;
  overflow: hidden;
  transition: transform 0.3s ease-out;
}
.tipser-cart-size-indicator.tipser-cart-size-indicator_animated {
  animation-name: elevator;
  animation-duration: 1s;
}
.tipser-cart-size-indicator.tipser-cart-size-indicator_infinity {
  text-indent: -99px;
}
.tipser-cart-size-indicator.tipser-cart-size-indicator_infinity:after {
  content: '+';
  display: block;
  width: 4px;
  height: 4px;
  background: white;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -2px;
  margin-top: -2px;
}
/********
* pulse *
*********/
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
/******/
/* Move indicator up and down and do a tiny heartbeat */
@keyframes elevator {
  0% {
    transform: translateY(0px);
    box-shadow: 0 0 0 0 #0000;
  }
  40% {
    transform: translateY(-6px);
    box-shadow: 0 0 0 1px #0005;
  }
  60% {
    transform: translateY(3px);
  }
  80% {
    transform: translateY(-1px);
  }
  100% {
    transform: translateY(0);
    box-shadow: 0 0 0 8px #0000;
  }
}
